import { Inject } from '@angular/core';
import { WINDOW } from '@ng-toolkit/universal';
import {
  Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef, OnDestroy
} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {DocumentationService} from '../../services/documentation.service';
import {Subject} from 'rxjs/index';
import {map, switchMap, takeUntil} from 'rxjs/internal/operators';
import {DomSanitizer} from "@angular/platform-browser";

@Component({
  selector: 'sug-documentation-loader',
  templateUrl: './documentation-loader.component.html',
  styleUrls: ['./documentation-loader.component.scss', '../../../../assets/helpers/ts-doc-styles.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DocumentationLoaderComponent implements OnInit, OnDestroy {
  public docHtml: any;
  private destroyer$: Subject<null> = new Subject();

  constructor(@Inject(WINDOW) private window: Window, private router: ActivatedRoute,
              private routing: Router,
              private documentation: DocumentationService,
              private domSanitizer: DomSanitizer,
              private cd: ChangeDetectorRef) {
  }

  ngOnInit() {
    this.router.data
      .pipe(map(data => data.body))
      .subscribe(html => this.setDoc(html));

    this.window.addEventListener('click', (e) => this.clickHandler(e));

  }

  ngOnDestroy(): void {
    this.window.removeEventListener('click', (e) => this.clickHandler(e));
    this.destroyer$.next();
    this.destroyer$.complete();
  }

  protected clickHandler(event: Event) {
    let element;
    if (event.srcElement.nodeName.toLowerCase() === 'em') {
      element = event.srcElement.closest('a');
    }


    if (element || ['a'].indexOf(event.srcElement.nodeName.toLowerCase()) > -1) {
      element = element || event.srcElement;
      if (element.dataset.pass) {
        return;
      }
      event.preventDefault();
      let href = element['href'].split(element['host'])[1];
      if (href.includes("#"))
        return;
      href = href.split('.html')[0];
      href = href.split('/').filter(param => !!param);
      let params;
      if (href.length === 2 && !( href[0] === 'framework' || href[1] === 'index' || href[1] === 'globals')) {
        params = [this.router.snapshot.params.module];
      } else if (href.length === 1 && !(href[0] === 'framework' || href[0] === 'globals')) {
        // return;
        params = [this.router.snapshot.params.module, this.router.snapshot.params.type];
      } else if (href[0] === 'globals') {
        params = [this.router.snapshot.params.module];
      } else {
        params = [];
      }

      params.push.apply(params, href);
      this.routing.navigate(['documentation', ...params]);
    }
  }

  private setDoc(docHtml) {
    this.docHtml = this.domSanitizer.bypassSecurityTrustHtml(docHtml);
    this.cd.markForCheck();
    // this.cd.detectChanges();
  }

}
