import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {catchError, map} from "rxjs/internal/operators";

@Injectable({
  providedIn: 'root'
})
export class DocumentationService {
  protected static readonly DOCUMENTATION_BASE_URI = "/assets/docs";

  constructor(private http: HttpClient) {
  }

  protected static getUri(...params: Array<string>) {
    return `${DocumentationService.DOCUMENTATION_BASE_URI}/${params.join("/")}`
  }

  public getDocumentation(module: string, type: string, name?: string) {

    const uri = !!name
      ? DocumentationService.getUri(module, type, name + '.html')
      : DocumentationService.getUri(module, type + '.html');
    return this.http.get(uri, {observe: 'response', responseType: "text"})
      .pipe(
        map((resp: any) => resp.body),
        catchError(err=> '<h1>Not Found</h1>')
      );
  }
}
