import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {DocumentationComponent} from './components/documentation/documentation.component';
import {DocumentationLoaderComponent} from './components/documentation-loader/documentation-loader.component';
import {DocumentationResolverService} from "./services/documentation-resolver.service";

@NgModule({
  exports: [RouterModule],
  imports: [RouterModule.forChild([
    {
      path: '',
      component: DocumentationComponent,
      children: [
        {
          path: ':module/:type/:name',
          component: DocumentationLoaderComponent,
          resolve: {
            body: DocumentationResolverService
          }
        },
        {
          path: ':module/:type',
          component: DocumentationLoaderComponent,
          resolve: {
            body: DocumentationResolverService
          }
        },
        {
          path: '',
          redirectTo: 'framework/index',
          pathMatch: 'full'
        }
      ]
    }
  ])]
})
export class DocumentationRoutingModule {
}
