import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, RouterStateSnapshot} from "@angular/router";
import {Observable} from "rxjs/index";
import {DocumentationService} from "./documentation.service";

@Injectable({
  providedIn: 'root'
})
export class DocumentationResolverService implements Resolve<HTMLElement> {
  constructor(private documentationService: DocumentationService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<HTMLElement> | Promise<HTMLElement> | HTMLElement {
    return this.documentationService.getDocumentation(route.params.module, route.params.type, route.params.name);
  }

}
